import { Component, EventEmitter, Input, Output } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { MatSelectChange } from '@angular/material/select';
import { FilterType } from '@core/enums';
import { AbstractFormControlValueAccessorComponent } from '../abstracts/form-control-value-accessor.abstract';

@Component({
    selector: 'findqo-dropdown',
    templateUrl: './dropdown.component.html',
    styleUrls: ['./dropdown.component.scss'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: DropdownComponent,
            multi: true,
        },
    ],
    standalone: false
})
export class DropdownComponent extends AbstractFormControlValueAccessorComponent {
	@Input() items: any = [];
	@Input() bindValueKey = 'key';
	@Input() bindLabelKey = 'value';
	@Input() bindLabelKeyArray = [];
	@Input() placeholder = '';
	@Input() hint?: string;
	@Input() selectClass?: string;
	@Input() wrapperClass?: string;
	@Input() type?: FilterType;

	// TODO: Check how to implement mat-select `selectionChange` event.
	@Output() changed = new EventEmitter<any>();

	onChange(event: MatSelectChange): void {
		this.changed.emit(event.value);
	}
}
